export default {
    data(){
        return{
            active:'people',
            menu:[{name:'',id:'people'},{name:'',id:'album'},{name:'',id:'adv'}]
        }
    },
    methods:{
        handleItem(item){
            this.active=item.id
            this.$router.push(`/admin/${item.id}`)
        }
    },
    mounted(){
        //
        if(!sessionStorage.getItem('isLogin')){
            this.$router.push('/admin/login')
        }
    }
}



