export default {
  data () {
    return {
      previews: {}, // 
      option: {
        img: '', //   ()
        size: 1, //   (:1)
        full: true, //  true  (:false)
        outputType: 'png', //   (:jpg)
        canMove: true, //   (:true)
        original: false, //   (:false)
        canMoveBox: true, //   (:true)
        autoCrop: true, //   (:false)
        // autoCropWidth: 400, //   (:80%)
        // autoCropHeight: 400, //   (:80%)
        fixedBox: false, //    (:false)
        fixed: false, //   (:true)
        fixedNumber: [ 16,9] //   (:[1:1])
      },
      downImg: '#'
    }
  },
  props: ['imgFile', 'fixedNumber'],
  methods: {
    changeScale (num) {
      // 
      num = num || 1
      this.$refs.cropper.changeScale(num)
    },
    rotateLeft () {
      // 
      this.$refs.cropper.rotateLeft()
    },
    rotateRight () {
      // 
      this.$refs.cropper.rotateRight()
    },
    Update () {
      // this.file = this.imgFile
      this.option.img = this.imgFile.url
    },
    realTime (data) {
      // 
      this.previews = data
    },
    uploadImg (type) {
      // 
      event.preventDefault()
      let that = this
      if (type === 'blob') {
        this.$refs.cropper.getCropBlob(data => {
          // blobfile
          let file = new window.File([data], this.imgFile.name, {type: data.type})
          that.$emit('upload', file)
        })
      } else {
        this.$refs.cropper.getCropData(data => {
          that.$emit('upload', data)
        })
      }
    }
  },
}



